


























export default {
    data() {
        var imgs = ['shuijiao', 'muwu', 'cbd']
        var list = []

        for (let i = 0; i < 40; i++) {
            list.push({
                src: `https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/${imgs[i%3]}.jpg`
            })
        }

        return {
            list: list
        }
    }
}



